.resume {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

.header {
  text-align: center;
}

.name {
  margin-top: 0;
}

.contact-info {
  font-size: 1.2rem;
  margin-bottom: 0;
}

.email {
  color: blue;
}

.summary {
  margin-top: 40px;
}

.education,
.work-experience,
.skills,
.references {
  margin-top: 20px;
}

h2 {
  margin-top: 0;
}

ul {
  list-style-type: disc;
  margin-top: 0;
  margin-bottom: 0;
}

li {
  margin-left: 20px;
}

p {
  margin-top: 0;
}
